import {postData} from "../services/requests";
import { postOnlyImg } from "../services/requests";

const forms = (state) => {
    const form = document.querySelectorAll('form'),
          inputs = document.querySelectorAll('input'),
          upload = document.querySelectorAll('[name="upload"]');

    // checkNumInputs('input[name="user_phone"]');

    const message = {
        loading: '...',
        success: '!    ',
        failure: '-   ...',
        spinner: 'assets/img/spinner.gif',
        ok: 'assets/img/ok.png',
        fail: 'assets/img/fail.png'
    };    



    const clearInputs = () => {
        inputs.forEach(item => {
            item.value = '';
        });
        upload.forEach(item => {
            item.previousElementSibling.textContent = '  ';
        });
    };

    upload.forEach(item => {
        item.addEventListener('input', () => {
            let dots;
            const arr =  item.files[0].name.split('.');
            arr[0] > 6 ? dots = '...' : dots = '.';
            const name =  arr[0].substring(0, 6) + dots +  arr[1];
            item.previousElementSibling.textContent = name;
        });
    });

    upload.forEach(item => {
        if (item.getAttribute('data-first') === 'true') {
            postOnlyImg(item, 'input');
        }
    });
    
    form.forEach(item => {
        item.addEventListener('submit', (e) => {
            e.preventDefault();

            let statusMessage = document.createElement('div');
            statusMessage.classList.add('status');
            item.parentNode.appendChild(statusMessage);

            item.classList.add('animated', 'fadeOut');
            setTimeout(() => {
                item.style.display = 'none';
            },400);

            let statusImg = document.createElement('img');
            statusImg.setAttribute('src', message.spinner);
            statusImg.classList.add('animated', 'fadeIn');
            statusMessage.appendChild(statusImg);

            let textMessage = document.createElement('div');
            textMessage.textContent = message.loading;
            statusMessage.appendChild(textMessage);

            const path = {
                designer: 'assets/server.php',
                question: 'assets/question.php'
            };

            const formData = new FormData(item);

            if (item.classList.contains('calc_form')){
                state.price = document.querySelector('.calc-price').textContent;
                for (let key in state) {
                    formData.append(key, state[key]);
                }
            }

            let api;
            item.closest('.popup-design') || item.classList.contains('calc_form') ? api = path.designer : api = path.question;
            console.log(api);


            postData(api, formData)
                .then(res => {
                    console.log(res);
                    statusImg.setAttribute('src', message.ok);
                    textMessage.textContent = message.success;
                })
                .catch(() => {
                    statusImg.setAttribute('src', message.fail);
                    textMessage.textContent = message.failure;
                })
                .finally(() => {
                    clearInputs();
                    setTimeout(() => {
                        statusMessage.remove();
                        item.style.display = 'block';
                        item.classList.remove('fadeOut');
                        item.classList.add('fadeIn');
                    }, 5000);
                });
        });
    });
};

export default forms;